@font-face {
  font-family: "PT Mono";
  src: url("../fonts/PTMono-Regular.woff2") format("woff2"), url("../fonts/PTMono-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/RobotoCondensed-Regular.woff2") format("woff2"), url("../fonts/RobotoCondensed-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/RobotoCondensed-Bold.woff2") format("woff2"), url("../fonts/RobotoCondensed-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap; }

html,
body {
  margin: 0;
  padding: 0;
  font-size: 14px;
  line-height: 21px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: normal;
  color: #000000;
  background-color: #f2f2f2;
  box-sizing: border-box; }

.overlay {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background: rgba(0, 0, 0, 0.5);
  z-index: 250; }

.hidden {
  display: none; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

.page-wrapper {
  width: 100%;
  margin: 0 auto;
  box-shadow: 0 5px 15px rgba(0, 1, 1, 0.2); }

@media (max-width: 1023px) {
  .page-wrapper {
    width: 100%;
    overflow: hidden;
    margin: 0 auto;
    box-shadow: 0 5px 15px rgba(0, 1, 1, 0.2); } }

@media (max-width: 767px) {
  .page-wrapper {
    width: 100%;
    overflow: hidden;
    margin: 0 auto;
    box-shadow: 0 5px 15px rgba(0, 1, 1, 0.2); } }

/**
  * ===========================================================
  * PAGE HEADER
  * ===========================================================
*/
.page-header {
  margin: 0 auto;
  padding: 0;
  display: flex;
  background-color: #1b2931; }

.page-header__logo {
  height: 0;
  position: relative; }

.page-header__img {
  position: absolute;
  top: 0;
  left: 0;
  width: 201px;
  height: 52px; }

.page-header__title {
  color: #ffffff;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 25px;
  font-weight: 700; }

.page-header__tip {
  color: #788891;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 12px;
  font-weight: 500; }

/**
  * ===========================================================
  * PAGE HEADER tablet
  * ===========================================================
*/
@media (max-width: 1023px) {
  .page-header {
    margin: 0 auto; }
  .page-header__img {
    position: absolute;
    top: 0;
    left: 0; } }

/**
  * ===========================================================
  * PAGE HEADER mobile
  * ===========================================================
*/
@media (max-width: 767px) {
  .page-header {
    margin: 0 auto;
    padding: 0; }
  .page-header__img {
    position: absolute;
    top: 0;
    left: 0; }
  .page-header__title {
    display: none; }
  .page-header__tip {
    display: none; } }

/**
  * ===========================================================
  * MAIN NAVIGATION desktop
  * ===========================================================
*/
.main-nav-wrapper {
  box-sizing: border-box;
  width: 1200px;
  margin-right: auto;
  margin-left: auto;
  padding: 0 26px; }

.main-nav {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  position: relative;
  margin: 0;
  padding: 0;
  color: #ffffff;
  font-family: "PT Mono", monospace;
  font-size: 16px;
  font-weight: 400;
  background-color: #1b2931; }

.main-nav__list {
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  list-style-type: none;
  overflow: hidden; }

.main-nav__item {
  width: 218px; }

.main-nav__link {
  display: block;
  padding: 50px 20px 50px 20px;
  text-align: center;
  text-decoration: none;
  cursor: pointer;
  position: relative;
  color: #ffffff; }
  .main-nav__link[href]:hover {
    background-color: #669ec0;
    cursor: pointer; }
  .main-nav__link[href]:active {
    background-color: #5496bd;
    color: #88b6d1;
    cursor: pointer; }
  .main-nav__link--mail::before {
    content: "";
    position: absolute;
    top: 10px;
    right: 12px;
    width: 15px;
    height: 11px;
    background-image: url("../img/icon-mail.svg");
    background-repeat: no-repeat; }
  .main-nav__link--phone::before {
    content: "";
    position: absolute;
    top: 10px;
    right: 12px;
    width: 17px;
    height: 17px;
    background-image: url("../img/icon-phone.svg");
    background-repeat: no-repeat; }

/**
    * ===========================================================
    * MAIN NAVIGATION tablet
    * ===========================================================
*/
@media (max-width: 1023px) {
  .main-nav-wrapper {
    box-sizing: border-box;
    width: 720px;
    margin-right: auto;
    margin-left: auto;
    padding: 0 26px; }
  .main-nav {
    margin: 0 auto; } }

/**
  * ===========================================================
  * MAIN NAVIGATION mobile
  * ===========================================================
*/
@media (max-width: 767px) {
  .main-nav-wrapper {
    box-sizing: border-box;
    width: 290px;
    margin-right: auto;
    margin-left: auto;
    padding: 0 26px; }
  .main-nav {
    margin: 0 auto;
    padding: 0; } }

.btn {
  color: #ffffff;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 700;
  letter-spacing: 0.7px;
  text-transform: uppercase;
  padding: 10px 22px 12px 21px;
  cursor: pointer;
  text-align: center;
  vertical-align: center;
  text-decoration: none;
  border: none;
  border-radius: 50px;
  background-color: #1b2931;
  border-radius: 5px;
  border: 1px solid #ffffff; }

.btn:hover {
  background-color: #00b2ff; }

.btn:active {
  background-color: #003e59; }

/**
* ===========================================================
* PAGE INTRO desktop
* ===========================================================
*/
.page-intro-wrapper {
  box-sizing: border-box;
  margin: 0 auto;
  padding: 0;
  background-color: #1b2931; }

.page-intro {
  width: 1200px;
  height: 532px;
  position: relative;
  margin: 0 auto;
  background-color: #1b2931; }

.page-intro__banner {
  position: absolute;
  top: 10px;
  right: 10px;
  background-image: url("../img/bg1.svg"), url("../img/bg2.jpg");
  background-repeat: no-repeat;
  background-position: right;
  background-size: contain, cover; }

.page-intro__header {
  margin: 0;
  color: #ffffff;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 80px;
  font-weight: 700;
  letter-spacing: 5.6px;
  text-transform: uppercase; }

.page-intro__tip {
  margin: 0;
  color: #ffffff;
  font-family: 'PT Mono', monospace;
  font-size: 22px;
  font-weight: 400;
  line-height: 36.09px; }

.btn-intro {
  border-radius: 5px;
  background-color: #00b2ff;
  color: #ffffff;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 700;
  letter-spacing: 0.8px;
  text-transform: uppercase; }

.page-intro__scroll {
  transform: rotate(90deg);
  opacity: 0.5;
  color: #ffffff;
  font-family: 'PT Mono', monospace;
  font-size: 14px;
  font-weight: 400; }

/**
* ===========================================================
* PAGE INTRO tablet
 * ===========================================================
*/
@media (min-width: 1023px) {
  .page-intro__banner {
    position: absolute;
    top: 10px;
    right: 10px;
    background-repeat: no-repeat;
    background-position: bottom;
    background-size: contain, cover; }
  .page-intro__header {
    margin: 0;
    transform: none;
    font-size: 60px;
    font-weight: 700;
    letter-spacing: 4.2px; }
  .page-intro__tip {
    margin: 0;
    font-size: 18px;
    line-height: 28.22px; }
  .page-intro__scroll {
    display: none; } }

/**
 * ===========================================================
* PAGE INTRO mobile
* ===========================================================
*/
@media (max-width: 767px) {
  .page-intro__banner {
    position: absolute;
    top: 10px;
    right: 10px;
    background-repeat: no-repeat;
    background-position: bottom;
    background-size: contain, cover; }
  .page-intro__header {
    margin: 0;
    transform: none;
    font-size: 40px;
    letter-spacing: 2.8px; }
  .page-intro__tip {
    margin: 0;
    font-size: 16px;
    line-height: 25.09px; }
  .page-intro__scroll {
    display: none; } }
